import { Component, OnInit } from '@angular/core';
import { WithdrawService } from '../../../../../auth/_services/withdraw.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../../../../../_services/data.service';
// import { NotificationsService } from 'angular4-notify';
import { ToastrService } from 'ngx-toastr';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { ModalDismissReasons, NgbDateStruct, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Router, ActivatedRoute } from '@angular/router';


@Component({
    selector: 'app-withdraw-list',
    templateUrl: "withdraw-list.component.html",
    styles: []
})
export class WithdrawListComponent implements OnInit {

    public withdrawData = "";
    public modalClose: string;


    constructor(private router: Router, private modalService: NgbModal, private imgBaseUrl: ScriptLoaderService, private toastr: ToastrService, private _withdrawservice: WithdrawService, private _dataService: DataService, private formBuilder: FormBuilder) { }


    ngOnInit() {



        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.withdraw_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this._withdrawservice.getAll().subscribe(
            data => {
                this.withdrawData = data.withdraw;
            }
        );
    }


    modalOpen(content) {
        this.modalService.open(content).result.then((result) => {
            this.modalClose = `Closed with: ${result}`;
        }, (reason) => {
            this.modalClose = `Dismissed ${this.modalDismissReason(reason)}`;
        });
    }

    private modalDismissReason(reason: any): string {
        if (reason === ModalDismissReasons.ESC) {
            return 'by pressing ESC';
        } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
            return 'by clicking on a backdrop';
        } else {
            return `with: ${reason}`;
        }
    }

    approved(withdraw: any) {
        console.log('withdraw');
        console.log(withdraw);
        this.toastr.success(withdraw.f_name + ' Amount Approved Successfully', 'Success Message');
        //this.router.navigate(['/withdraw/withdraw-list']);
    }

    rejected(withdraw: any) {
        console.log('withdraw');
        console.log(withdraw);

        this.toastr.success(withdraw.f_name + ' Amount Rejected Successfully', 'Success Message');
        //this.router.navigate(['/withdraw/withdraw-list']);
    }


}
