import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WithdrawListComponent } from '../withdraw-list/withdraw-list.component';
import { RouterModule, Routes } from '@angular/router';
import { LayoutModule } from '../../../../layouts/layout.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DefaultComponent } from '../../../default/default.component';
import { NgxPaginationModule } from 'ngx-pagination';
import { QuillModule } from 'ngx-quill';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

const routes: Routes = [
    {
        path: "",
        component: DefaultComponent,
        children: [
            {
                path: "",
                component: WithdrawListComponent
            }
        ]
    },
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes),
        LayoutModule, NgbModule.forRoot(), FormsModule, ReactiveFormsModule, NgxPaginationModule, QuillModule,
    ],
    declarations: [WithdrawListComponent]

})
export class WithdrawListModule { }
